import { IconButton as ButtonIcon } from "@rp/react/web/mui/core"
import { Close, Search } from "@rp/react/web/mui/icons"
import { SearchBar } from "./SearchBar"

const HeaderSearchBar = ({
  filterTerm,
  onChange,
  placeholder,
  maxWidth,
}: {
  filterTerm: string
  onChange: (value: string) => void
  placeholder: string
  maxWidth?: number | string
}) => {
  return (
    <SearchBar
      maxwidth={maxWidth}
      size="small"
      placeholder={placeholder}
      variant="outlined"
      inputProps={{
        "aria-label": "filter",
        role: "input",
      }}
      InputProps={{
        startAdornment: <Search />,
        endAdornment: filterTerm.length ? (
          <ButtonIcon size="small" onClick={() => onChange("")} sx={{ w: 16 }}>
            <Close />
          </ButtonIcon>
        ) : null,
      }}
      value={filterTerm}
      onChange={e => onChange(e.target.value)}
      autoComplete="off"
      name="header-search-bar-filter-input"
    />
  )
}

export { HeaderSearchBar }
